import { Lists } from './../../models/lists.model';
import { ListPage } from './../list/list';
import { Component, Input } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { SongsService } from './../../services/songs.service';
import { Songs } from './../../models/songs.model';

/**
 * Generated class for the RightNavPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-right-nav',
  templateUrl: 'right-nav.html',
})
export class RightNavPage {

  list:Songs[] = [];
  index:number;
  inListIndex:number;
  song:Songs;

  constructor(public navCtrl: NavController, public navParams: NavParams, private songsService: SongsService) {
    this.index = navParams.get("index");
    this.list = navParams.get("lista");

    if(!this.list){
      this.song = this.songsService.getSong(this.index);
    }else{
      this.song = this.findSongInList(this.index);
    }   
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RightNavPage');
  }

  canNext(){
    if(this.list){
      return this.inListIndex < this.list.length-1 ? true:false;
    }else if(this.index < this.songsService.getSongs().length-1){
      return true;
    }
    return false;
  }

  next(){
    if(this.canNext()){
      this.index++;
      this.inListIndex++;
      this.getSong();
    }else{
      this.index--;
      this.inListIndex--;
    }
  }

  canPrev(){
    if(this.list){
      return this.inListIndex > 0 ? true:false;
    }else if(this.index > 0){
      return true;      
    }  
    return false;
  }

  prev(){
    if(this.canPrev()){
      this.index--;
      this.inListIndex--;      
      this.getSong();
    }else{
      this.index++;
      this.inListIndex++;            
    }
  }

  checker(){
    this.navCtrl.pop();
  }

  getSong(){
    if(!this.list){
      this.getSongInSongs(this.index);
    }else{
      this.getSongInList(this.inListIndex);
    }    
  }

  findSongInList(index:number){
    for(let i in this.list){
      if(this.list[i].ID == this.index){
        this.inListIndex = Number.parseInt(i);
        return this.list[i]; 
      } 
    }
  }

  getSongInList(index:number){
    this.song = this.list[index];
  }

  getSongInSongs(index:number){
    this.song = this.songsService.getSong(index);
  }

}



